"use client";

import Link from "next/link";
import type { THeader } from "@/types";

import { Logo } from "@/components/custom/logo";
import { Navigation } from "@/components/custom/navigation";
import { Button } from "@/components/ui/button";
import { HeaderWrapper, useHeaderContext } from "@/components/custom/layouts/header-wrapper";


interface IHeaderProps {
  data?: THeader | null;
}

function HeaderButton({ href, label }: { href: string; label: string }) {
  const { isActive } = useHeaderContext();
  
  return (
    <Link href={href} className="absolute top-1/2 -translate-y-1/2 right-4">
      <Button className={`font-sans font-bold text-md italic px-4 py-3 transition-colors duration-300 ${
        isActive 
          ? "bg-brand-red text-white hover:bg-brand-red/90" 
          : "bg-brand-pink text-brand-red hover:bg-brand-red/90"
      }`}>
        {label}
      </Button>
    </Link>
  );
}

export function Header({ data }: IHeaderProps) {
  if (!data) return null;

  const { logoText, ctaButton } = data;
  return (
    <HeaderWrapper>
      <HeaderButton href={ctaButton.href} label={ctaButton.label} />
      <Logo data={logoText} />
      <div className="flex items-center gap-4 mt-4">
        <Navigation />
      </div>
    </HeaderWrapper>
  );
}